<template>
	<view class="content">
		<view class="inputnumbox">
			<!-- <input type="text"  :disabled = 'typeofs' confirm-type="done" @input="inputtype" v-model=types focus="true" placeholder="类型" adjust-position="false"
			 style="font-size: 68rpx;height: 70rpx;" /> -->
			<xfl-select :list="list" :clearable="false" :showItemNum="5" :listShow="false" :isCanInput="true" :style_Container="'height: 50px; font-size: 16px;'"
			 :placeholder="'请选择'" :selectHideType="'hideAll'" v-model="types" @change="change">
			</xfl-select>
			<input type="number" confirm-type="done" @input="inputnum" v-model=money placeholder="输入金额(必填)" adjust-position="false"
			 style="font-size: 48rpx;margin-top: 30rpx;height: 70rpx;background-color: #F4F5F6;padding: 8rpx;" />
			<input v-model="beizhu" type="text" maxlength=15 placeholder="备注" style="font-size: 48rpx;margin-top: 30rpx;height: 70rpx;background-color: #F4F5F6;padding: 8rpx;" />
		</view>
		<view class="text-area">
			<button type="primary" class="queren" @click="queren">确认</button>

		</view>
		<view class="text-area">
			<button type="warn" class="qingchu" @click="clears()">清除</button>

		</view>
	</view>
</template>

<script>
	import xflSelect from '../../components/xfl-select/xfl-select.vue';

	export default {
		components: {
			xflSelect
		},
		data() {
			return {
				list: [
					'医疗', '服饰', '食品', '餐饮', '其他'
				],
				title: 'Hello',
				types: "",
				money: '',

				beizhu: "",
				typeofs: false,
				storeMoney: uni.getStorageSync("addmoney") || 0,
			}
		},
		onLoad() {


		},
		methods: {
			change({
				newVal,
				oldVal,
				index,
				orignItem
			}) {

				// console.log(newVal, oldVal, index, orignItem);
				this.types = newVal;
			},
			// 确认

			queren() {
				if (this.money == "") {

				} else {
					// console.log();
					if (this.types == "") {
						this.types = this.list[4];
					}
					try {
						uni.setStorageSync('addmoney', parseFloat(this.storeMoney) + parseFloat(this.money));
					} catch (e) {
						// console.log(e);
					}
					uni.navigateBack();
				}

			},
			// 清除所有
			clears() {
				// this.types = "";
				this.money = "";
				this.beizhu = "";

			},
			// 输入时
			inputnum() {


			},
			inputtype() {

			}
		}
	}
</script>

<style>
	.inputnumbox {
		/* background-color: #007AFF; */
		margin-top: 20rpx;
		width: 605rpx;

	}

	.queren {
		float: center;
		width: 600rpx;
		margin-top: 40rpx;
	}

	.qingchu {
		float: center;
		width: 600rpx;
		margin-top: 10rpx;
	}

	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	.logo {
		height: 200rpx;
		width: 200rpx;
		margin-top: 200rpx;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 50rpx;
	}

	.text-area {
		display: flex;
		justify-content: center;
	}

	.title {
		font-size: 36rpx;
		color: #8f8f94;
	}
</style>
